.navbar {
    display: flex;
    justify-content: space-around;
    margin: auto;
    align-items: center;
    height: 75px;
    position: fixed;
    top: 0;
    min-width: 412px;
    width: 100vw;
    z-index: 10001;
    background-color: rgba(0, 0, 0, 0.703);
}

.nav-icon-container {
    display: flex;
    justify-content: space-around;
    width: 80px;
}

.nav-icon {
    display: grid;
    place-content: center;

    width: 20px;
    height: 20px;
    font-size: 20px;
    color: var(--light);
    transition: 0.4s;
}

.nav-icon:hover {
    transform: scale(1.5);
    color: var(--primary);
}

.hamburger-container {
    display: grid;
    place-content: center;
    padding: 10px 0;
}

.hamburger .close-hamburger {
    width: 60px;
    height: auto;
}

.ham-span {
    display: block;
    width: 50px;
    height: 3px;
    margin: 5px auto;
    background-color: var(--light);
    border-radius: 10px;

    transition: 0.4s;
}

.hamburger-container:hover span {
    background-color: var(--primary);
}

.hamburger-container:hover span:nth-child(1) {
    transform: translateX(10px);
    rotate: 45deg;
}

.hamburger-container:hover span:nth-child(2) {
    display: none;
}

.hamburger-container:hover span:nth-child(3) {
    transform: translate(5px, 5px);
    rotate: -45deg;
}

/* close */
.hamburger-container:hover span {
    background-color: var(--primary);
}

.close-hamburger span:nth-child(1) {
    transform: translateX(10px);
    rotate: 45deg;
}

.close-hamburger span:nth-child(2) {
    display: none;
}

.close-hamburger span:nth-child(3) {
    transform: translate(5px, 5px);
    rotate: -45deg;
}

.dropdown-close {
    position: absolute;
    top: 0;
    right: 0;
    margin: 20px;
    padding-right: 20px;
}

.show-nav-items {
    position: absolute;
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-size: 50px;
    font-weight: 800;
    background-color: var(--dark);
    top: 0;
    left: 0;
}

.nav-item {
    text-decoration: none;
    color: var(--light);
    margin: 10px 30px;
    position: relative;
}

.nav-item::after {
    content: "";
    height: 3px;
    width: 0;
    background: var(--primary);
    position: absolute;
    bottom: 0;
    left: 0;
    transition: width 0.4s;
}

.nav-item:hover::after {
    width: 100%;
}

@media (max-width: 1000px) {
    .nav-icon-container {
        display: none;
    }
}
